@import 'reset';

.beer-info {
  padding: 15px;
  z-index: 1;
  position: fixed;
}

.beer-info li {
  padding: 5px;
  border: 1px solid black;
  width: 400px;
  background-color: white;
  color: black;
}

html, body {
	height: 100%;
	margin: 0px;
	padding: 0px
}

body {
	font-family: "Open Sans", sans-serif;
	/* font-family: 'Libre Baskerville', serif; */
	color: #686868;
	font-size: 15px;
	background-color: #6B7283;
}


.clearfix:after {
   content: " ";
   display: block;
   height: 0;
   clear: both;
}

.clear{
	clear: both;
}

h1, h2, h3, h4, h5, h6 {
	margin-bottom: 17px;
	font-weight: 300;
	/* color: #ff5400; */
}

h1 {
	font-size: 28px;
}

h2 {
	font-size: 26px;
}

h3 {
	font-size: 24px;
	margin-bottom: 15px;
}

ul{
	list-style-type: square;
	margin-left: 60px;
	margin-bottom: 30px;
}

ul li{
	line-height: 1.9;
}


.zipSearch{
	display: none;
}

/* Popup */
.markerPop{

}

@media only screen and (min-width: 361px) {

	p {
		margin-bottom: 15px;
		line-height: 20px;
	}

	#map-canvas{
		height: 100%;
		border-left: 1px solid black;

	}
	#control{
		float: left;
		color: #D8D8DD;
		width: 15%;
		padding: 50px 30px 10px 30px;
	}

	#control input[type=text]{
		width: 100%;
		height: 40px;
		font-size: 24px;
		background-color: #6B7283;
		border: 1px solid #BE757E;
	}

	.learnButton {
		margin-top: 10px;
		margin-bottom: 20px;
		background: #BE757E;
		border: 0;
		color: #D8D8DD;
		height: 37px;
		line-height: 37px;
		width: 100%;
		text-align: center;
		cursor: pointer;
	}

	#mc_embed_signup{
	margin-top: 30px;
	float: left;
	width: 300px;
	}

	#mc_embed_signup form label{
		margin-bottom: 30px;
		line-height: 20px;
		clear: right;
	}

	#mc_embed_signup form input.email{
		width: 100%;
		height: 40px;
		font-size: 24px;
		background-color: #6B7283;
		border: 1px solid #BE757E;
	}
}

/* phones */
@media only screen and (max-width: 360px) {

	p {
		margin-bottom: 10px;
		line-height: 26px;
	}

	#map-canvas{
		height: 100%;
	}

	#control{
		color: #D8D8DD;
		padding: 20px 30px 10px 30px;
	}

	#control input[type=text]{
		width: 100%;
		height: 40px;
		font-size: 24px;
		background-color: #6B7283;
		border: 1px solid #BE757E;
	}

	.learnButton {
		float: left;
		margin: 0px 10px 0px 10px;
		margin-top: 20px;
		margin-bottom: 20px;
		background: #BE757E;
		border: 0;
		color: #D8D8DD;
		height: 37px;
		line-height: 37px;
		width: 40%;
		text-align: center;
	}

	#mc_embed_signup{
		display: none;
	}

	#mc_embed_signup form label{
		margin-bottom: 30px;
		line-height: 20px;
		clear: right;
	}

	#mc_embed_signup form input.email{
		width: 100%;
		height: 40px;
		font-size: 24px;
		background-color: #6B7283;
		border: 1px solid #BE757E;
	}
}
